import React from "react";
import Typography from "@material-ui/core/Typography";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";

import { themeOverWrites } from "./DefaultDialog.stories.styles";
import Theme from "../../Theme";
import Dialog from "../Dialog";

const useStyles = makeStyles(themeOverWrites);

type DefaultDialogStoryProps = {
    btn: string;
    title: string;
    content: string;
    mode: string;
};

type Action = {
    color: string;
    label: string;
    callBack: () => void;
};

const actions: Action[] = [
    {
        color: "default",
        label: "Cancelar",
        callBack: () => alert("Cancelar")
    },
    {
        color: "primary",
        label: "Ok,entendi",
        callBack: () => alert("Ok")
    }
];

export const DefaultDialog = ({ mode, btn, title, content }: DefaultDialogStoryProps): React.ReactElement => {
    const classes = useStyles();

    return (
        <Paper className={classes[mode]} id="dialogs">
            <Typography variant="h3">Dialog</Typography>
            <br />
            <Divider />
            <br />
            <Dialog btn={btn} title={title} content={content} actions={actions} />
        </Paper>
    );
};

const argsOptions = {
    args: {
        btn: "Abrir Dialog",
        title: "Titulo",
        content: "ConteÃºdo",
        mode: "whiteBg"
    },
    argTypes: {
        btn: { type: "string" },
        title: { type: "string" },
        content: { type: "string" },
        mode: {
            type: "select",
            options: ["whiteBg", "darkBg"]
        }
    }
};

storiesOf("Components/Dialog", module)
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Badge", args => <DefaultDialog {...args} />, argsOptions);
